@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  background-color: #fff;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  border-radius: 3px;
}

.card img {
  width: 100%;
}

.card-info {
  color: #1d2228;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem 0.75rem 1rem;
  letter-spacing: 0.5px;
}

.card-info h3 {
  margin: 0;
}

.card-info small,
.card h3 {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.overview {
  background-color: #fff;
  padding: 1rem 1.1rem;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 100%;
  transform: translateY(101%);
  overflow-y: auto;
  transition: transform 0.3s ease-in;
  color: #1d2228;
  font-size: 0.9rem;
  cursor: default;
}

.overview h4 {
  margin: 1rem 0 0;
}

.card:hover .overview {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .card-info {
    padding: 0.5em;
  }
  .card-info h3 {
    font-size: 0.8em;
  }

  .card-info small {
    font-size: 0.6em;
  }

  .overview {
    font-size: 0.7em;
    padding: 1em;
  }
}

.watching--backdrop {
  position: relative;
  height: 30em;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
  border-radius: 3px;
}

.backdrop--info-mobile {
  visibility: hidden;
}

.backdrop--info {
  background: rgb(0, 0, 0);
  background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3));
  color: #f1f1f1;
  width: 100%;
  height: 100%;
  display: flex;
  padding-left: 2.5em;
  justify-content: center;
  flex-direction: column;
}

.backdrop--info-title {
  font-size: 4em;
}

.backdrop--info-overview {
  margin-top: 0.5em;
  font-size: 0.95em;
  max-width: 35%;
}

@media (max-width: 800px) {
  .backdrop--info {
    visibility: hidden;
  }

  .backdrop--info-mobile {
    visibility: visible;
  }

  .watching--backdrop {
    height: 10em;
  }

  .backdrop--info-title {
    font-size: 1.5em;
    margin: 0.25em 0;
  }

  .backdrop--info-overview {
    width: 100%;
    max-width: 100%;
  }
}

h2 {
  margin: 0;
}

.container--no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.fc-film-reel {
  font-size: 10em;
}

.no-results--btn {
  background-color: #fb8122;
  color: #fff;
  font-size: 1.1em;
  padding: 1em;
  margin: 1em 0 0;
}

.no-results--btn:hover {
  transform: scale(0.98);
}

.container--no-results small {
  text-align: center;
}

.card-group {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.container h2 {
  margin: 1em 0;
}

.card-group--content {
  margin: 1rem 0;
  width: calc((100vw -1em) / 6);
}

@media (max-width: 1024px) {
  .card-group--content {
    width: calc((100vw -1em) / 5);
  }
}

@media (max-width: 768px) {
  .card-group--content {
    width: calc((100vw -1em) / 4);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  padding: 0.5rem 1rem;
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.trigger-button {
  background-color: #272d36;
  width: 125px;
  padding: 0.75rem 0;
  border-radius: 1px;
  display: inline-block;

  border-bottom: 1px solid #e1e2e2;
  position: relative;
  color: #fff;
}

.trigger-button:focus {
  outline: #fb8122 solid 2px;
}

.dropdown-menu {
  height: 160px;
  width: 125px;
  background-color: #272d36;

  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 1;
}

.dropdown-menu--item {
  color: #fff;
  width: 100%;
  text-align: left;
  border-radius: 0;
  background-color: transparent;
}

.dropdown-menu--item:hover {
  text-decoration: line-through;
  color: #fb8122;
}

.dropdown-menu .active {
  background-color: #e1e2e2;
}

.menu button {
  text-transform: uppercase;
  cursor: pointer;
  padding: 1rem;
  background-color: transparent;
  border: none;
  font-size: 1rem;
  color: #fff;
  height: 40px;

  letter-spacing: 0.05em;
}

.menu .active {
  color: #fb8122;
  font-weight: 700;
}

.menu button:hover {
  color: #fb8122;
  text-decoration: line-through;
}

.menu button:focus {
  outline: none;
}

.active:hover {
  color: #fff;
}

.pagination {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #e1e2e2;
  width: 250px;
  height: 50px;
  color: #1d2228;
}

.pagination-number {
  text-align: center;
  width: 150px;
  padding: 0 10px;
}

.divider {
  background-color: rgba(29, 34, 40, 0.05);
  width: 1px;
  height: 100%;
  padding: 0;
}

.pagination button {
  background-color: transparent;
  border: none;
  width: 48px;
  height: 48px;
  font-size: 1.25em;
  cursor: pointer;
  padding: 0;
}

.pagination button:hover {
  background-color: rgba(29, 34, 40, 0.05);
}

.pagination button:focus {
  outline: #fb8122 solid 1px;
}

html {
  transition: all 0.4s ease-in;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #1d2228;
  font-family: 'IBM Plex Sans', sans-serif;
  margin: 0 auto;
  padding: 2em 4em;
  color: #fff;
  height: 100vh;
  transition: 0.4s ease;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: #1d2228;
  width: calc(100vw - 9em);
  padding: 2em 0;
}

.app-header h1,
.app-header--mobile h1 {
  margin: 0 1em 0 0;
  color: #fb8122;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
}

.app-main {
  margin-top: 5em;
}

.search {
  background-color: transparent;
  border: 2px solid #e1e2e2;
  border-radius: 50px;
  font-family: inherit;
  font-size: 1rem;
  padding: 0.5rem 1rem;
}

.search::-webkit-input-placeholder {
  color: #fff;
}

.search:-ms-input-placeholder {
  color: #fff;
}

.search::-ms-input-placeholder {
  color: #fff;
}

.search::placeholder {
  color: #fff;
}

.search:focus {
  outline: none;
  background-color: #e1e2e2;
  color: #1d2228;
}

.app-header--nav {
  display: flex;
  flex: 2 1;
}

.app-header--mobile {
  display: none;
}

.section-pagination {
  background-color: #e1e2e2;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 800px) {
  body {
    padding: 2em;
  }

  .app-header {
    display: none;
  }

  .app-header--mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .app-header--mobile-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
  }

  .search {
    width: 80vw;
  }

  .app-main {
    margin: 0;
  }
}

